// import detectEthereumProvider from '@metamask/detect-provider';
import Web3 from 'web3';
import { ETH_NETWORK } from './constants';
import Config from '../../config';
import ctx from '../index';
import { showConfirm } from '../../components/Modal';
import { getColAmt, getPairedAmt, getBTokenAmt, isApproveCol, isApprovePaired } from './transaction';
import { getBumperAmt, getLpBalance, getLpStaked, getPoolLpSupply, getPoolFarmStaked, isApproveLP, getPoolLpReserve } from './farmTransaction';
import { getColPrice, getBcTokenPrice, getBumperPrice } from './price';
import { getLpApy, getLendApy, getBorrowApy } from './apy';
import { alertError, getDefaultTheme } from '../../utils';

const crt_net = Config.NETWORK
const MARKET_POOL_LIST = Config.MARKET_POOL_LIST

// 初始化metamask，初始化链上数据
export const initChain = async () => {
  // 异步加载 @metamask 优化体积
  const detectEthereumProvider = await import(
    /* webpackChunkName: 'metamask' */
    '@metamask/detect-provider'
  ).then(m => m.default);
  // Detect the MetaMask Ethereum provider
  // this returns the provider, or null if it wasn't detected
  const provider = await detectEthereumProvider();
  if (provider) {
    if (provider === window.ethereum) {
      ctx.data.chainProvider = provider;
      const network = await provider.request({
        method: 'net_version',
        params: [],
      });
      console.log('current network is:', network, ETH_NETWORK[network]);
      if (crt_net != network) {
        alertError('please change to correct network')
        return 1
      }
      // 注册web3 provider
      ctx.data.web3 = new Web3(provider);
    } else {
      showConfirm({
        title: 'Please only use MetaMask!',
        content: 'Do you have multiple wallets installed?',
        onOk() {
          window.location.href = 'https://metamask.io/download.html';
        }
      });
    }
  } else {
    // ctx.data.web3 = new Web3.providers.HttpProvider("http://localhost:8545");
    showConfirm({
      title: 'Please install MetaMask!',
      content: 'You have not install MetaMask, jump link to install?',
      onOk() {
        window.location.href = 'https://metamask.io/download.html';
      }
    });
  }
};

export const initTheme = () => {
  let defaultTheme = getDefaultTheme();
  ctx.data.theme = defaultTheme;
}

// 更新账号信息，Market页
export const updateAccount = async (account) => {
  console.log('updateAccount market......');
  if (account && account.length) {
    const chainAccount = account[0];
    if (!ctx.data.chainAccount) {
      ctx.data.chainAccount = chainAccount;
    }
    // paired
    await getPairedAmt(chainAccount);
    await getBumperAmt(chainAccount); // 获取bumper的数目
    // 并行处理
    await Promise.all([
      new Promise((resolve) => {
        (async () => {
          // approve
          await isApproveCol(); // 是否授权了btcb
          await isApprovePaired(); // 是否授权了paired
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          for (let item of MARKET_POOL_LIST) {
            await getColAmt(item.col, chainAccount);
          }
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          for (let item of MARKET_POOL_LIST) {
            await getBTokenAmt(item.colE, chainAccount);
          }
          resolve()
        })();
      }),
    ]).then(() => { });
  } else {
    ctx.data.chainAccount = 0;
  }
}

// 更新账号信息, Farm页
export const updateAccountFarm = async (account) => {
  console.log('updateAccount farm ......');
  if (account && account.length) {
    const chainAccount = account[0];
    if (!ctx.data.chainAccount) {
      ctx.data.chainAccount = chainAccount;
    }
    // 并行处理
    await Promise.all([
      new Promise((resolve) => {
        (async () => {
          // approve
          await isApproveLP(); // 是否授权了lp
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          for (let item of MARKET_POOL_LIST) {
            await getLpBalance(item.colE, chainAccount); // 获取col对应的btoken生成的lp的数目
          }
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          for (let item of MARKET_POOL_LIST) {
            await getLpStaked(item.colE, chainAccount); // 获取col对应的btoken生成的lp的stake的数目
          }
          resolve()
        })();
      })
    ]).then(() => { });
  }
}

// refreshReward
export const refreshReward = async (account) => {
  console.log('refreshReward farm ......');
  if (account && account.length) {
    for (let item of MARKET_POOL_LIST) {
      await getLpStaked(item.colE, account[0]); // 获取col对应的btoken生成的lp的stake的数目
    }
  }
}

// 更新supply总量的数据
export const updateSupply = async () => {
  console.log('updateSupply ......');
  for (let item of MARKET_POOL_LIST) {
    await Promise.all([
      new Promise((resolve) => {
        (async () => {
          await getPoolLpSupply(item.colE);// 获取lp totalSupply
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          await getPoolLpReserve(item.colE);
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          await getPoolFarmStaked(item.colE);
          resolve()
        })();
      })
    ])
  }
}

// 更新价格
export const updatePrice = async () => {
  console.log('updatePrice ......');
  let colIdList = []
  let tmp = []
  for (let item of MARKET_POOL_LIST) {
    await getBcTokenPrice(item.colE);
    if(colIdList.indexOf(item.col)===-1){
      colIdList.push(item.col)
      tmp.push(item)
    }
  }
  for (let item of tmp) {
    await getColPrice(item.col, item.colIndex);
  }
  // bumper
  await getBumperPrice();
}

// 更新价格
export const updateApy = async () => {
  console.log('updateApy ......');
  for (let item of MARKET_POOL_LIST) {
    await Promise.all([
      new Promise((resolve) => {
        (async () => {
          await getLpApy(item.colE, item.rewardPerWeek);
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          await getLendApy(item.colE, item.expiryDate);
          resolve()
        })();
      }),
      new Promise((resolve) => {
        (async () => {
          await getBorrowApy(item.colE, item.expiryDate);
          resolve()
        })();
      })
    ])
  }
}