import ctx from '../index';

export const getLpApy = async (col, rewardPerWeek) => {
  const { poolLpSupplyMap, bcPriceMap, bumperPrice } = ctx.data;
  const lpTotalTmp = poolLpSupplyMap[col]
  const bcPrice = bcPriceMap[col]
  const rewardValue = (rewardPerWeek / 7) * bumperPrice;
  const lpValue = (bcPrice + 1) * lpTotalTmp; // lp的价格应该等于bctoken价格+paired价格，等于+1基准
  const apy = Math.round(10000 * 365 * rewardValue / lpValue) / 100;
  let map = ctx.data.lpApyMap || {}
  map[col] = apy;
  ctx.data.lpApyMap = map
  // console.log('apy', stakeLpTmp, lpTotalTmp, bcPrice, apy)
  return
}

export const getLendApy = async (col, expiryDate) => {
  const { bcPriceMap } = ctx.data;
  const bcPrice = bcPriceMap[col]
  const expiry = Math.floor((new Date(expiryDate)).getTime() / 1000);
  const now = Math.floor((new Date()).getTime() / 1000);
  // 假设本金是1，到期后可以拿到的bc为(1 / bcPrice)，那么interest为 （1 / bcPrice）- 1
  const interest = (1 / bcPrice) - 1
  const duration = (expiry - now) / (60 * 60 * 24) // 天数
  let map = ctx.data.lendApyMap || {}
  map[col] = duration > 0 ? Math.round((10000 * interest / duration) * 365) / 100 : 0
  ctx.data.lendApyMap = map
  return
}

export const getBorrowApy = async (col, expiryDate) => {
  const { bcPriceMap } = ctx.data;
  const bcPrice = bcPriceMap[col]
  const expiry = Math.floor((new Date(expiryDate)).getTime() / 1000);
  const now = Math.floor((new Date()).getTime() / 1000);
  // 假设本金是1，实际上可以抵押mintRatio后为1，即到时候要偿还1，现在拿到手为 1 * bcPrice，那么借款方要付出interest为 (1-bcPrice)
  const interest = (1 - 1 * bcPrice)
  const duration = (expiry - now) / (60 * 60 * 24) // 天数
  let map = ctx.data.borrowApyMap || {}
  map[col] = duration > 0 ?  Math.round((10000 * interest / duration) * 365) / 100 : 0
  ctx.data.borrowApyMap = map
  return
}

